import {
	mapActions,
	mapState,
	mapMutations
} from 'vuex'
import {
	uniLoadMore
} from '@dcloudio/uni-ui'

export default {
	data() {
		return {
			numPage:0,
			dataList:[],
			more:'more'
		}
	},
	components:{
		uniLoadMore
	},
	computed:{
		...mapState(['orderList','orderCount'])
	},
	filters:{
		joinName(arr){
			let str = arr.reduce((x,y)=>x+y.name+'、','')
			str = str.substring(0,str.length-1)
			return str.substr(0,9)
		}
	},
	onLoad() {
		this.initialize()
	},
	async onReachBottom(){
		console.log(this.orderList.length,this.orderCount)
		if(this.orderList.length<this.orderCount){
			this.more = 'loading'
		}else{
			this.more = 'noMore'
			return
		}
		try{
			this.numPage++
			await this.getList()
		}catch(err){
			this.more = 'more'
		}
		console.log('加载更多')
	},
	async onPullDownRefresh() {
		await this.initialize()
		uni.stopPullDownRefresh();
	},
	methods: {
		...mapActions(['getOrderList']),
		...mapMutations(['SET_VALUE']),
		initialize(){
			this.numPage = 0
			this.SET_VALUE({key:'orderList',value:[]})
			this.getList()
		},
		async getList(){
			await this.getOrderList({
				params:{
					condition:{
						pageNum:this.numPage
					}
				},
				stateName:'orderList'
			})
			// this.dataList=this.dataList.concat(this.orderList)
		},
		openDetail(obj){
			this.SET_VALUE({key:'handleOrder',value:obj})
			uni.navigateTo({
				url:"../order_detail/order_detail"
			})
		}
		
	}
}
